<template>
    <div>
        <h3 style="text-align: left">Dodavanje beleski</h3>
        <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-group
                id="input-group-1"
                label="Naslov:"
                label-for="input-1"
            >
                <b-form-input
                    id="input-1"
                    v-model="form.title"
                    type="text"
                    required
                ></b-form-input>
            </b-form-group>

            <b-form-group
                id="input-group-1"
                label="Kategorija:"
                label-for="input-1"
                description="U trenutnoj verziji se obaveze ne razvrstavaju po kategorijama, ali ih i dalje imaju."
            >
                <b-form-input
                    id="input-1"
                    v-model="form.category"
                    type="text"
                    required
                ></b-form-input>
            </b-form-group>

            <b-form-group
                id="input-group-2"
                label="Sadrzaj:"
                label-for="input-2"
            >
                <b-form-textarea
                    id="input-2"
                    v-model="form.content"
                    required
                ></b-form-textarea>
            </b-form-group>

            <b-button-group>
                <b-button type="submit" variant="outline-success"
                    >Dodaj</b-button
                >
                <b-button type="reset" variant="outline-danger"
                    >Resetuj</b-button
                >
            </b-button-group>
        </b-form>
    </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
    name: "NewNote",
    data() {
        return {
            form: {
                title: "",
                category: "",
                content: "",
            },
            show: true,
        };
    },
    methods: {
        ...mapActions(["addNote"]),
        onSubmit(evt) {
            evt.preventDefault();
            this.addNote(this.form);
            this.makeToast();
            this.form.title = "";
            this.form.category = "";
            this.form.content = "";
            this.show = false;
            this.$nextTick(() => {
                this.show = true;
            });
        },
        onReset(evt) {
            evt.preventDefault();
            this.form.title = "";
            this.form.category = "";
            this.form.content = "";
            this.show = false;
            this.$nextTick(() => {
                this.show = true;
            });
        },
        makeToast(append = false) {
            this.toastCount++;
            this.$bvToast.toast(`Uspesno ste dodali belesku!`, {
                title: "Uspesno!",
                autoHideDelay: 3000,
                variant: "success",
                appendToast: append,
            });
        },
    },
};
</script>

<style lang="scss" scoped></style>
