<template>
    <div>
        <h3 style="text-align: left">Izmena beleske</h3>
        <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-group
                id="input-group-1"
                label="Naslov:"
                label-for="input-1"
            >
                <b-form-input
                    id="input-1"
                    v-model="form.title"
                    type="text"
                    required
                ></b-form-input>
            </b-form-group>

            <b-form-group
                id="input-group-1"
                label="Kategorija:"
                label-for="input-1"
                description="U trenutnoj verziji se obaveze ne razvrstavaju po kategorijama, ali ih i dalje imaju."
            >
                <b-form-input
                    id="input-1"
                    v-model="form.category"
                    type="text"
                    required
                ></b-form-input>
            </b-form-group>

            <b-form-group
                id="input-group-2"
                label="Sadrzaj:"
                label-for="input-2"
            >
                <b-form-textarea
                    id="input-2"
                    v-model="form.content"
                    required
                ></b-form-textarea>
            </b-form-group>

            <b-button-group>
                <b-button type="submit" variant="outline-success"
                    >Izmeni</b-button
                >
                <b-button type="reset" variant="outline-danger"
                    >Resetuj</b-button
                >
            </b-button-group>
        </b-form>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
    name: "Note",
    data() {
        return {
            form: {
                title: "",
                category: "",
                content: "",
            },
            id: this.$route.params.id,
            show: true,
        };
    },
    computed: {
        ...mapState(["notes"]),
        note: function() {
            for (let i = 0; i < this.notes.length; i++)
                if (this.notes[i].id === parseInt(this.$route.params.id))
                    return this.notes[i];
            return null;
        },
    },
    methods: {
        ...mapActions(["changeNote"]),
        onSubmit(evt) {
            evt.preventDefault();
            this.changeNote({ note: this.form, id: this.id });
            this.makeToast();
            this.show = false;
            this.$nextTick(() => {
                this.show = true;
            });
        },
        onReset(evt) {
            evt.preventDefault();
            this.show = false;
            this.$nextTick(() => {
                this.show = true;
            });
        },
        makeToast(append = false) {
            this.toastCount++;
            this.$bvToast.toast(`Uspesno ste dodali belesku!`, {
                title: "Uspesno!",
                autoHideDelay: 3000,
                variant: "success",
                appendToast: append,
            });
        },
    },
    mounted: function() {
        this.form.title = this.note.title;
        this.form.category = this.note.category;
        this.form.content = this.note.content;
    },
};
</script>

<style lang="scss" scoped></style>
